const LogoDesignEditor = defineComponent({
  components: {
    CheckboxButtonEditorControl,
    CheckboxButtonGroupEditorControl,
    CheckboxToggleEditorControl,
    ColorBoxEditorControl,
    ComboBoxEditorControl,
  },
  props: {
    value: {
      required: false,
      default: undefined,
      type: Object as PropType<LogoSettings>,
    },
    content: {
      required: false,
      default: undefined,
      type: Object as PropType<LogoData>,
    },
    defaults: {
      required: true,
      type: Object as PropType<LogoDefaults>,
    },
    siteSettings: {
      required: false,
      default: undefined,
      type: Object as PropType<GlobalDesign>,
    },
    textSizeList: {
      required: true,
      type: Array as PropType<number[]>,
    },
    colorList: {
      required: true,
      type: Array as PropType<string[]>,
    },
    autoColorEnabled: {
      required: true,
      type: Boolean,
    },
    alphaColorEnabled: {
      required: true,
      type: Boolean,
    },
  },
  setup(props, { root, emit }) {
    const i18n = useI18N(root.$store);

    const settings = computed(() => ({
      font: props.value?.font,
      size: props.value?.size,
      bold: props.value?.bold ?? props.defaults.bold,
      italic: props.value?.italic ?? props.defaults.italic,
      color: props.value?.color,
      spacing: props.value?.spacing ?? props.defaults.spacing,
      capitalization: props.value?.capitalization ?? props.defaults.capitalization,
      frame: {
        visible: props.value?.frame?.visible ?? props.defaults.frame?.visible,
        width: props.value?.frame?.width ?? props.defaults.frame?.width,
        color: props.value?.frame?.color,
      },
      visible: props.value?.visible ?? props.defaults.visible,
    }));

    const isTextLogo = computed(() => props.content?.type === LogoType.TEXT);
    const isImageLogo = computed(() => props.content?.type === LogoType.IMAGE);

    const font = computed({
      get: () => getFontIfExists(settings.value.font),
      set: useFontChangeHandler(settings, emit),
    });

    const fontChoices = computed(() => {
      const defaultValue = calculateActualFontDefault(
        props.defaults.font,
        props.siteSettings,
      );
      const defaultFontText = i18n('CommonLabels.Logo.Font.Default')
        .replace('{fontName}', fontFamilyValue(fontWithFallback(defaultValue)) ?? '');

      return [
        { key: undefined, label: defaultFontText },
        ...fontKeyLabelList(),
      ];
    });

    const size = computed({
      get: () => settings.value.size,
      set: useSizeChangeHandler(settings, emit),
    });

    const sizeChoices = computed(() => {
      const defaultValue = calculateActualTextSizeDefault(
        props.defaults.size,
        props.siteSettings,
      );
      const defaultSizeText = i18n('CommonLabels.Logo.Size.Default')
        .replace('{sizeInPixels}', defaultValue?.toString() ?? '');
      const baseSizeChoices = props.textSizeList
        .map((textSize) => ({ key: textSize, label: textSize.toString() }));

      return [
        { key: undefined, label: defaultSizeText },
        ...baseSizeChoices,
      ];
    });

    const bold = computed({
      get: () => settings.value.bold,
      set: useBoldChangeHandler(settings, emit),
    });

    const italic = computed({
      get: () => settings.value.italic,
      set: useItalicChangeHandler(settings, emit),
    });

    const defaultColor = computed(() => calculateActualColorDefault(
      props.defaults.color,
      props.siteSettings,
    ));
    const color = computed({
      get: () => settings.value.color,
      set: useColorChangeHandler(settings, emit),
    });

    const spacing = computed({
      get: () => settings.value.spacing,
      set: useSpacingChangeHandler(settings, emit),
    });
    const spacingChoices = range(0, 21)
      .map((spacingItem) => ({ key: spacingItem, label: spacingItem.toString() }));

    const capitalization = computed({
      get: () => settings.value.capitalization,
      set: useCapitalizationChangeHandler(settings, emit),
    });
    const capitalizationChoices = [
      {
        key: CapitalizationType.NONE,
        label: i18n('CommonLabels.Logo.Capitalization.None'),
      },
      {
        key: CapitalizationType.ALL,
        label: i18n('CommonLabels.Logo.Capitalization.All'),
      },
      {
        key: CapitalizationType.SMALL,
        label: i18n('CommonLabels.Logo.Capitalization.Small'),
      },
    ];

    const frameVisibility = computed({
      get: () => settings.value.frame.visible,
      set: useFrameVisibleChangeHandler(settings, emit),
    });

    const frameWidth = computed({
      get: () => settings.value.frame.width,
      set: useFrameWidthChangeHandler(settings, emit),
    });
    const frameWidthChoices = range(1, 6)
      .map((spacingItem) => ({ key: spacingItem, label: spacingItem.toString() }));

    const defaultFrameColor = computed(() => calculateActualColorDefault(
      props.defaults.frame?.color,
      props.siteSettings,
    ));
    const frameColor = computed({
      get: () => settings.value.frame.color,
      set: useFrameColorChangeHandler(settings, emit),
    });

    const visibility = computed({
      get: () => settings.value.visible,
      set: useVisibleChangeHandler(settings, emit),
    });

    return {
      i18n,
      isTextLogo,
      isImageLogo,
      bold,
      italic,
      defaultColor,
      color,
      size,
      sizeChoices,
      font,
      fontChoices,
      spacing,
      spacingChoices,
      capitalization,
      capitalizationChoices,
      frameVisibility,
      frameWidth,
      frameWidthChoices,
      defaultFrameColor,
      frameColor,
      visibility,
    };
  },
});

export default LogoDesignEditor;
