import { Color } from '@/app/shared/client/type/color';
import { Font } from '@/app/shared/client/type/font';
import {
  GlobalColorsString,
  GlobalFontsString,
  GlobalTextSizeString,
} from '@/app/shared/client/type/global-string';

export interface FrameSettings {
  visible?: boolean;
  width?: number;
  color?: Color;
}

interface FrameDefaults {
  visible?: boolean;
  width?: number;
  color?: Color | GlobalColorsString;
}

export enum CapitalizationType {
  NONE = 'none',
  ALL = 'all',
  SMALL = 'small',
}

export interface LogoSettings {
  visible?: boolean;
  bold?: boolean;
  italic?: boolean;
  color?: Color;
  size?: number;
  font?: Font;
  spacing?: number;
  capitalization?: CapitalizationType;
  frame?: FrameSettings;
}

export interface LogoDefaults {
  visible?: boolean;
  bold?: boolean;
  italic?: boolean;
  color?: Color | GlobalColorsString;
  size?: number | GlobalTextSizeString;
  font?: Font | GlobalFontsString;
  spacing?: number;
  capitalization?: CapitalizationType;
  frame?: FrameDefaults;
}
